import axios from 'axios';

// SERVER URL
const BASE_URL = 'https://apis.data.go.kr/1360000/VilageFcstInfoService_2.0';
const GET_ULTRA_SRT_NCST = '/getUltraSrtFcst';
const SERVICE_KEY = '7RzOAYDkB9qRHVsXHVLPuEAUsikSSpD4YqMjJ47VbykQRu+GF6nvvmzo6K72vQ3aIJBHN/1p2uSVEhJm7B01BA==';

//   ( )
let nx = 60;
let ny = 127;

// API   Date 
const today = new Date();

// API   
export async function getWetherInfo(cb) {
  //   
  const time = getTime();
  const date = getToday();
  const res = await axios({
    url: `${BASE_URL}${GET_ULTRA_SRT_NCST}`,
    method: 'GET',
    params: {
      serviceKey: `${SERVICE_KEY}`,
      pageNo: '1',
      numOfRows: '1000',
      dataType: 'JSON',
      base_date: date,
      base_time: time,
      nx: nx,
      ny: ny
    }
  });

  const data = res.data;
  const items = data.response.body.items.item ?? [];
  console.log(items);
  const weatherInfo = parseWeatherData(items);
  cb(weatherInfo, time);
}

function getToday() {
  const month = today.getMonth() + 1 < 9 ? `0${today.getMonth() + 1}` : String(today.getMonth() + 1);
  const day = today.getDate() < 9 ? `0${today.getDate()}` : String(today.getDate());

  return `${today.getFullYear()}${month}${day}`;
}

function getTime() {
  // 45   
  if (today.getMinutes() < 45) {
    if (today.getHours() === 0) {
      today.setDate(today.getDate() - 1);
      today.setHours(23);
      today.setMinutes(30);
    } else {
      today.setHours(today.getHours() - 1);
      today.setMinutes(30);
    }
  } else if (today.getMinutes() < 45) {
    today.setMinutes(0);
  }

  let hours = today.getHours() < 9 ? `0${today.getHours()}` : String(today.getHours());
  let minutes = today.getMinutes() < 9 ? `0${today.getMinutes()}` : String(today.getMinutes());

  return hours + minutes;
  //  
  // return '0000';
}

// T1H : 
// RN1 : 1 
// UUU :  (+), (-)
// VVV :  (+), (-)
// REH : 
// PTY :  () (0), (1), /(2), (3), (5), (6), (7)
// VEC : 
// WSD : 
// SKY :     : (1), (3), (4)
function parseWeatherData(datas) {
  //    
  const time = datas[0].fcstTime;
  const nowDatas = datas.filter(data => {
    if (data.fcstTime === time) {
      return data;
    }
  });
  console.log(nowDatas);

  //       
  let weatherInfo = {};
  nowDatas.forEach(data => {
    weatherInfo[data.category] = data.fcstValue;
  });

  return weatherInfo;
}

export function getIconByWeather(sky, pty, lgt, time) {
  let imgSrc = '';
  console.log('time : ', time);
  const dayFlag = parseInt(time) > 1800 || parseInt(time) < 600 ? 'N' : 'D';

  if (dayFlag === 'D') {
    if (lgt !== '0') {
      imgSrc = '/d_storm.png';
    } else if (pty === '0' && sky === '0') {
      imgSrc = '/d_sun.png';
    } else if (pty === '0' && sky !== '0') {
      imgSrc = '/d_clouds.png';
    } else if (pty === '1' || pty === '5') {
      imgSrc = '/d_rain.png';
    } else {
      imgSrc = '/d_snow.png';
    }
  } else {
    if (lgt !== '0') {
      imgSrc = '/n_storm.png';
    } else if (pty === '0' && sky === '0') {
      imgSrc = '/n_moon.png';
    } else if (pty === '0' && sky !== '0') {
      imgSrc = '/n_clouds.png';
    } else if (pty === '1' || pty === '5') {
      imgSrc = '/n_rain.png';
    } else {
      imgSrc = '/n_snow.png';
    }
  }
  return imgSrc;
}

